import { TagCloud } from "react-tagcloud";
import "../styles/canvas_2.css";

const data = [
  { value: "JavaScript", count: 10 },
  { value: "React", count: 10 },
  { value: "Nodejs", count: 10 },
  { value: "Express.js", count: 10 },
  { value: "HTML5", count: 10 },
  { value: "MongoDB", count: 10 },
  { value: "CSS3", count: 10 },
];

const customRenderer = (tag, size, color) => (
  <span
    key={tag.value}
    style={{
      animation: "blinker 3s linear infinite",
      animationDelay: `${Math.random() * 2}s`,
      border: `2px solid ${color}`,
      margin: "3px",
      padding: "3px",
      display: "inline-block",
      color: "white",
    }}
  >
    {tag.value}
  </span>
);

export const SimpleCloud = () => (
  <TagCloud
    className="text-sphere"
    minSize={12}
    maxSize={35}
    tags={data}
    disableRandomColor={true}
    renderer={customRenderer}
  />
);
